body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  color: #e0e0e0;
}

.container {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–Ω–æ–ø–æ–∫ –º–µ–Ω—é ‚Äî –æ–≤–∞–ª—å–Ω–æ–µ –º–µ–Ω—é –≤–Ω–∏–∑—É */
.menu-button-container {
  position: fixed;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  background-color: rgba(30, 30, 30, 0.6);
  padding: 15px 30px;
  border-radius: 26px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  z-index: 1000;
  box-shadow: 0 0 2px rgba(255, 255, 255, 0.6);
}

/* –°—Ç–∏–ª—å –¥–ª—è –∫–Ω–æ–ø–æ–∫ */
.menu-button {
  background-color: transparent;
  color: #e0e0e0;
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 6px;
  min-width: 60px;
  text-align: center;
  transition: color 0.3s ease;
  letter-spacing: 1px;
}

/* –°—Ç–∏–ª—å –¥–ª—è –∏–∫–æ–Ω–æ–∫ */
.menu-button .menu-icon {
  font-size: 26px;
  color: #cccccc;
  line-height: 1;
}

/* –ó–µ–ª–µ–Ω—ã–π —Ü–≤–µ—Ç –∏–∫–æ–Ω–∫–∏ —É –∞–∫—Ç–∏–≤–Ω–æ–π –∫–Ω–æ–ø–∫–∏ */
.menu-button.active .menu-icon {
  color: #6FCF00;
}

/* –ó–µ–ª–µ–Ω—ã–π —Ü–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ —É –∞–∫—Ç–∏–≤–Ω–æ–π –∫–Ω–æ–ø–∫–∏ */
.menu-button.active {
  color: #6FCF00;
}
